@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&family=Roboto:wght@400;500&display=swap');

* {
  margin: 0;
  padding: 0;
}


body {
  background: #fff;
  color: #222;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
}

.wrapper {
  width: 1200px;
  margin: 50px auto;
}
header {
  position: relative;
}
header .logo {
  font-weight: 600;
  font-size: 20px;
}

header ul.nav {
  float: right;
  list-style: none;
}

header ul.nav li {
  display: inline;
  margin-left: 25px;
  cursor: pointer;
  transition: opacity 500ms ease;
}
header ul.nav li:hover {
  opacity: 0.5;
}

header .shop-cart-button {
  float: right;
  cursor: pointer;
  transition: color, transform  500ms esse;
  }

header .shop-cart-button:hover,
header .shop-cart-button.active {
  color: #dc3d3d;
  transform: scale(1.5);
  }

header .shop-cart {
  position: absolute;
  border: solid 4px #333;
  border-radius: 10px;
  min-height: 50px;
  top: 30px;
  right: 0;
  width: 450px;
  background: #fafafa;
  box-shadow: 21px 20px 19px -17px rgba(35,29,29,1);
  -webkit-box-shadow: 21px 20px 19px -17px rgba(35,29,29,1);
  -moz-box-shadow: 21px 20px 19px -17px rgba(35,29,29,1);
  z-index: 1000;
  padding: 20px;
  padding-bottom: 0px;
}

header .shop-cart .empty h2 {
  margin-bottom: 20px;
  font-size: 20px;
}
header .shop-cart .item {
  width: 100%;
  float: left;
  margin-bottom: 20px;
}

header .shop-cart .item  img {
  width: 70px;
  height: 80px;
  float: left;
  margin-right: 20px;
}

header .shop-cart .item h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

header .shop-cart .item p {
  color: #797979;
  font-weight: 600;
}
header .presentation {
  margin: 50px 0;
  background: url('./img/pexels-vecislavas-popa-1643383.jpg') no-repeat;
  width: 100%;
  height: 500px;
  background-size: cover;
  background-position: center center;
  background-blend-mode: multiply;
  background-color: #bcbcbc;
  position: relative;
}

header .presentation::after {
  content: "Кращі товари для вашого дому";
  position: absolute;
  top: 100px;
  left: 50px;
  width: 300px;
  font-size: 40px;
  font-weight: 600;
  color: #fff
}

header .presentation::before {
  content: "за вигідною ціною";
  position: absolute;
  top: 250px;
  left: 50px;
  width: 300px;
  font-size: 15px;
  font-weight: 300;
  color: #fff
}

.categories div {
  display: inline-block;
  background: #f2f2f2;
  border-radius: 50px;
  padding: 10px 20px;
  margin-bottom: 25px;
  margin-right: 15px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 500ms esse;
}
.categories div:hover {
  border-color: silver;
  transform: scale(1.1);
}
footer {
  text-align: center;
  margin-top: 10px;
}

main {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
 
}

main .item {
  position: relative;
  width: 30%;
  margin-bottom: 50px;
  background-color: #f5f5f5;
  overflow: hidden;
  padding-bottom: 20px;
}

main .item img, .full-item img {
  width: 100%;
  height: 250px;
  border-radius: 10px 10px 0 0;
  transition: transform 500ms ease;
  cursor: pointer;
}

main .item img:hover,
.full-item img:hover {
  transform: scale(1.08);
}

main h2, .full-item h2,
main p, .full-item p,
main b, .full-item {
  margin: 10px 20px;
  color: #333;
}
main b, .full-item b {
  color: #5fa365;
  margin-left: 20px;
}

main .add-to-card,
.full-item .add-to-card {
  position: absolute;
  right: 20px;
  bottom: 20px;
  background: #ca6969;
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  color: #fff;
  border-radius: 50%;
  cursor: pointer;
  font-weight: 600;
  transition: transform 500ms ease;
}
main .add-to-card:hover,
.full-item .add-to-card:hover {
  transform: scale(1.5) translateY(-5px);
}

header .shop-cart .item .delete-icon {
  position: relative;
  color:#e47878;
  float: right;
  top: -25px;
  cursor: pointer;
  transition: color, transform 500ms ease;
}
header .shop-cart .item .delete-icon:hover {
  color: #fc0505;
  transform: scale(1.5);
}

header .shop-cart .suma {
  float: left;
  width: 100%;
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 20px;
}
.full-item {
  position: fixed;
  top:0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 9999;
  overflow: auto;
}
.full-item img {
  width: 450px;

   margin: 0 auto;
  display: block;
}
.full-item > div {
  width: 450px;
  position: relative;
  margin: 10% auto;
  padding: 40px 30px;
  background: #f2f2f2;
  border-radius: 9px;
}